import { createApp } from 'vue'
import App from './App.vue'
import InfiniteList from './components/InfiniteList.vue';
import ImageCarousel from './components/ImageCarousel.vue'; // 使用新的名称
import FormComponent from './components/FormComponent.vue';
import ModalDialog from './components/ModalDialog.vue'; // 使用新的名称
import NavigationMenu from './components/NavigationMenu.vue';
import ImagePreview from './components/ImagePreview.vue';


const app = createApp(App);
app.component('InfiniteList', InfiniteList);
app.component('ImageCarousel', ImageCarousel);
app.component('FormComponent', FormComponent);
app.component('ModalDialog', ModalDialog);
app.component('NavigationMenu', NavigationMenu);
app.component('ImagePreview', ImagePreview);

app.mount('#app');